'use client';
import { useEffect, useState } from 'react';
import { useDashboardStore } from '@/store/useDashboardStore';

export default function WidgetCard({ widget }: { widget: any }) {
  const [data, setData] = useState<any>(null);
  const removeWidget = useDashboardStore((state) => state.removeWidget);

  const fetchData = async () => {
    try {
      const res = await fetch(widget.apiUrl);
      const json = await res.json();
      setData(json);
    } catch (err) { console.error(err); }
  };

  useEffect(() => {
    fetchData();
    const interval = setInterval(fetchData, widget.refreshInterval * 1000);
    return () => clearInterval(interval);
  }, [widget.apiUrl, widget.refreshInterval]);

  return (
    <div className="p-4 bg-gray-900 border border-gray-700 rounded-lg text-white">
      <div className="flex justify-between items-center mb-4">
        <h3 className="font-bold">{widget.name}</h3>
        <button onClick={() => removeWidget(widget.id)} className="text-red-500 text-sm">Remove</button>
      </div>
      <div className="text-green-400 font-mono">
        {data ? JSON.stringify(data).slice(0, 100) + "..." : "Loading..."}
      </div>
    </div>
  );
}